import VueRouter from "vue-router";

export default new VueRouter({
  mode: "hash",
  routes: [
    {
      path: "/",
      redirect: "home",
    },
    {
      path: "/home",
      name: "home",
      component: () => import("../page/YHome.vue"),
    },
    {
      path: "/plane",
      name: "plane",
      component: () => import("../page/YPlane.vue"),
    },
    {
      path: "/train",
      name: "train",
      component: () => import("../page/YTrain.vue"),
    },
    {
      path: "/self",
      name: "self",
      component: () => import("../page/YSelf.vue"),
      children: [
        {
          path: "/",
          redirect: "info",
        },
        {
          path: "info",
          name: "selfInfo",
          component: () => import("../page/self/YSelfInfo.vue"),
        },
        {
          path: "order",
          name: "selfOrder",
          component: () => import("../page/self/YSelfOrder.vue"),
        },
        {
          path: "passenger",
          name: "selfPassenger",
          component: () => import("../page/self/YSelfPassenger.vue"),
        },
        {
          path: "help",
          name: "selfHelp",
          component: () => import("../page/self/YSelfHelp.vue"),
        },
      ],
    },
  ],
});
