import Vue from "vue";

import Vuex from "vuex";
import axios from "axios";
import webConfig from "@/data/config";

Vue.use(Vuex);

const user = {
  namespaced: true,
  state: {
    token: localStorage.getItem("token") || "",
    userForm: {
      usrAccount: "",
      usrAvatar: "",
      usrEmail: "",
      usrId: "",
      usrPhone: "",
      usrVipLevel: 0,
    },
  },
  actions: {
    updateUserInfo(context, { _this, data }) {
      console.log(_this, data);
      axios({
        url: `${webConfig.url}/usr/update`,
        method: "patch",
        headers: {
          token: context.state.userForm.token,
          "Content-Type": "multipart/form-data",
        },
        data,
        timeout: 10000,
      })
        .then((value) => {
          if (value.data.code == 200) {
            context.state.userForm = value.data.data;
            _this.$notify({
              title: "成功",
              message: value.data.message,
              type: "success",
              duration: 1000,
            });
          } else {
            _this.$notify({
              title: "失败",
              message: value.data.message,
              type: "error",
              duration: 1000,
            });
          }
        })
        .catch((error) => {
          _this.$message({
            message: error,
            type: "error",
            duration: 1000,
          });
        });
    },
  },
};

const register = {
  namespaced: true,
  state: {
    rDV: false,
    address: "",
  },
  mutations: {
    changeRegisterDialog(state) {
      state.rDV = !state.rDV;
    },
  },
  actions: {
    checkRegister(context, { account, password, phone, email, _this }) {
      axios({
        method: "post",
        url: `${webConfig.url}/usr/registry`,
        data: {
          usrAccount: account,
          usrEmail: email,
          usrPhone: phone,
          usrPwd: password,
        },
        timeout: 10000,
      })
        .then((value) => {
          if (value.data.code == 200) {
            _this.$message({
              message: value.data.message,
              type: "success",
              duration: 1000,
            });
            _this.loading = false;
            _this.changeRegisterDialog();
            _this.changeLoginDialog();
          } else {
            _this.$message({
              message: value.data.message,
              type: "error",
              duration: 1000,
            });
            _this.loading = false;
          }
        })
        .catch((error) => {
          _this.$message({
            message: error,
            type: "error",
            duration: 1000,
          });
          _this.loading = false;
          console.log(error);
        });
    },
  },
  getters: {},
};

const login = {
  namespaced: true,
  state: {
    lDV: false,
    fDV: false,
    address: "",
    loginState: false,
  },
  mutations: {
    changeLoginDialog(state) {
      setTimeout(() => {
        state.lDV = !state.lDV;
      }, 300);
    },
    changeFoundDialog(state) {
      setTimeout(() => {
        state.fDV = !state.fDV;
      }, 300);
    },
  },
  actions: {
    checkLogin(context, { account, password, _this }) {
      axios({
        url: `${webConfig.url}/usr/login`,
        params: { usrName: account, usrPassword: password },
        timeout: 10000,
      })
        .then((value) => {
          console.log(value.data.code);
          if (value.data.code == 200) {
            _this.$message({
              message: value.data.message,
              type: "success",
              duration: 1000,
            });
            localStorage.setItem("token", value.data.data);
            context.state.loginState = true;
            _this.loading = false;
            _this.changeLoginDialog();
            context.dispatch("getInfo", { _this });
          } else {
            _this.$message({
              message: value.data.message,
              type: "error",
              duration: 1000,
            });
            _this.loading = false;
          }
        })
        .catch((error) => {
          _this.$message({
            message: error,
            type: "error",
            duration: 1000,
          });
          _this.loading = false;
          console.log(error);
        });
    },
    getInfo(context, { _this }) {
      axios({
        url: `${webConfig.url}/usr/info`,
        method: "get",
        headers: { token: localStorage.getItem("token") },
        params: { token: localStorage.getItem("token") },
      })
        .then((value) => {
          if (value.data.code == 200) {
            context.rootState.user.userForm = value.data.data;
            _this.$message({
              message: value.data.message,
              type: "success",
              duration: 1000,
            });
          } else {
            _this.$router.push("/");
            context.state.loginState = false;
            context.rootState.user.userForm = {
              usrAccount: "",
              usrAvatar: "",
              usrEmail: "",
              usrId: "",
              usrPhone: "",
              usrVipLevel: 0,
            };
            _this.$message({
              message: value.data.message,
              type: "error",
              duration: 1000,
            });
          }
        })
        .catch((error) => {
          _this.$router.push("/");
          context.state.loginState = false;
          _this.$message({
            message: error,
            type: "error",
            duration: 1000,
          });
        });
    },
  },
  getters: {},
};

const lost = {
  namespaced: true,
  state: {
    checkCode: "",
  },
  actions: {
    send(context, { eP, _this }) {
      axios({
        url: `${webConfig.url}/usr/before-update/${eP}`,
      })
        .then((value) => {
          console.log(value.data.data);
          context.state.checkCode = value.data.data;
          _this.$notify({
            title: "成功",
            message: "验证码已成功发送，请及时查收",
            type: "success",
          });
        })
        .catch((error) => {
          _this.$message({
            message: error,
            type: "error",
            duration: 1000,
          });
          console.log(error);
        });
    },
    alter(context, { pW, pW2, _this }) {
      axios({
        url: `${webConfig.url}/usr/before-update`,
        params: {
          pw: pW,
          pW2: pW2,
        },
      })
        .then((value) => {
          context.state.checkCode = value.data.code;
          _this.$message({
            message: value.data.message,
            type: "error",
            duration: 1000,
          });
        })
        .catch((error) => {
          _this.$message({
            message: error,
            type: "error",
            duration: 1000,
          });
          console.log(error);
        });
    },
  },
};
const plane = {
  namespaced: true,
  state: {},
  actions: {},
};
export default new Vuex.Store({
  modules: {
    user,
    register,
    login,
    lost,
    plane,
  },
});
